.app-header {
  align-items: flex-end;
}

.app-header h1 {
  flex-grow: 1;
}

.app-header h2 {
  font-size: 1.2rem;
  color: grey;
}
.todo-list-item {
  font-size: 1.25rem;
}

.todo-list-item button {
  width: 35px;
  margin: 3px;
}

.todo-list-item-label {
  margin-left: 1.25rem;
  line-height: 35px;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.todo-list-item.done .todo-list-item-label {
  text-decoration: line-through;
}

.todo-list-item.important .todo-list-item-label {
  font-weight: bold;
  color: steelblue;
}

.todo-list .list-group-item {
  padding: .25rem .75rem;
}

.search-panel {
  margin: 1rem 0;
}

.search-panel .search-input {
  width: auto;
  flex-grow: 1;
  margin-right: 3px;
}

.bottom-panel {
  margin-top: 10px;
}

.bottom-panel .new-todo-label {
  width: auto;
  flex-grow: 1;
  margin-right: 3px;
}

.alert-modal {
  position: fixed;
  top: 40vh;
  left: 50vw;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  width: 600px;
  background-color: white;
}

.overlay {
  position: fixed; 
  display: block;
  width: 100%; 
  height: 100%;
  top: 0; 
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.5);
  z-index: 2;
}

.note-header h1 {
  font-weight: 700;
  font-size: 80px;
  color: #fff;
  opacity: 0.7;
}

.note-header header {
  background-color: #4f5a6e;
}

footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 60px;
}
.todo-app {
  margin: 2rem auto 0 auto;
  max-width: 500px;
}

